.wrapper {
  //   display: inline-flex;
  //   align-items: center;
  //   justify-content: center;
  width: fit-content;
  min-width: fit-content;
  border-radius: 4px;
  font-size: 1.6rem;
  // font-weight: 500;
  display: flex;
  flex-direction: row;
  cursor: pointer;
  background-color: transparent;
  border: 1px solid transparent;
  user-select: none;
  // + .wrapper {
  //   margin-left: 8px;
  // }
  &.disabled {
    pointer-events: none;
    opacity: 0.5;
  }
}

.rounded {
  border-radius: 999px;
  box-shadow: 0 2px 8px rgb(0 0 0 / 6%);
  border-color: rgba(22, 24, 35, 0.12);

  &:hover {
    border-color: rgba(22, 24, 35, 0.2);
    background-color: rgba(22, 24, 35, 0.03);
  }
}
.primary {
  color: var(--white);
  background-color: var(--primary);
  border-color: var(--primary);

  &:hover {
    border-color: var(--primary);
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.06), rgba(0, 0, 0, 0.06)),
      #fe2c55;
  }
}

.outline {
  color: var(--primary);
  border-color: currentColor;

  &:hover {
    border-color: currentColor;
    background-color: rgba(254, 44, 85, 0.06);
  }
}

.text {
  &:hover {
    text-decoration: underline;
  }
}
.medium {
  min-width: 88px;
  padding: 4px 16px;
}
.small {
  min-width: 48px;
  padding: 4px 16px;
}

.large {
  padding: 14px 16px;
  min-width: 140px;
}
.icon {
  display: inline-block;
  width: 24px;
  text-align: center;
  padding-right: 6px;
}
